import { Link } from 'react-router-dom';

export function NavBar() {

  const handleIncompleteLink = (e) => {
    e.preventDefault();
    alert(' .');
  };

  return (
    <nav className="navbar">
      <ul>
        <li><Link to="/">Home</Link></li>
        <li><Link to="/auditions">  </Link></li>
        <li><Link to="/mentoring"> </Link></li>
        <li><Link to="/announcement" ></Link></li>
        <li><Link to="/promotion" ></Link></li>
        <li><Link to="/cooperation" ></Link></li>
        <li><Link to="/buysell" ></Link></li>
        <li><Link to="/free" ></Link></li>
        <li className="login"><Link to="/" onClick={(e) => handleIncompleteLink(e)}></Link></li>
      </ul>
    </nav>
  );
}